interface RuleCaptureConfig<T> {
    name: string;
    patterns: T[];
}

interface RuleCapturesConfig<T> {
    [name: string]: RuleCaptureConfig<T>;
    [index: number]: RuleCaptureConfig<T>;
}

/* #region  raw rule config types */

type LevelConfig = {
    patterns?: RuleConfig[];
    repository?: RepositoryConfig;
};

type RepositoryConfig = {
    [name: string]: RuleConfig | LevelConfig;
};

type RuleConfigType<T> = {
    comment?: string;
    disabled?: boolean;
} & T;

type RuleConfig = RuleConfigType<
    RuleIncludeConfig | RuleMathConfig | RuleBeginConfig
>;

type RuleIncludeConfig = {
    include: string;
};

type RuleMathConfig = {
    match: string;
    name?: string;
    captures?: RuleCapturesConfig<RuleConfig>;
};

type RuleBeginConfigType<T> = {
    begin: string;
    name?: string;
    beginCaptures?: RuleCapturesConfig<RuleConfig>;
    captures?: RuleCapturesConfig<RuleConfig>;
} & T;

type RuleBeginConfig = {
    begin: string;
    name?: string;
    beginCaptures?: RuleCapturesConfig<RuleConfig>;
    captures?: RuleCapturesConfig<RuleConfig>;
} & (RuleEndConfig | RuleWhileConfig);

type RuleEndConfig = {
    end: string;
    contentName?: string;
    patterns?: RuleConfig[];
    endCaptures?: RuleCapturesConfig<RuleConfig>;
};

type RuleWhileConfig = {
    while: string;
    whileCaptures?: RuleCapturesConfig<RuleConfig>;
};

/* #endregion */

enum RuleType {
    INCLUDE,
    BEGIN_END,
    BEGIN_WHILE,
    MATCH,
}

type RuleGenType<T> = {
    comment?: string;
    disabled?: boolean;
    type: RuleType;
} & T;

type Rule = RuleGenType<RuleIncludeFields | RuleMathFields | RuleBeginFields>;

/**
 * @from_type RuleIncludeConfig
 */
type RuleIncludeFields = {
    include: string;
};

/**
 * @from_type RuleMathConfig
 */
type RuleMathFields = {
    match: RegExp;
    name?: string;
    captures?: RuleCapturesConfig<Rule>;
};

type RuleBeginFieldsType<T> = {
    begin: RegExp;
    name?: string;
    beginCaptures?: RuleCapturesConfig<Rule>;
    captures?: RuleCapturesConfig<Rule>;
} & T;

/**
 * @from_type RuleBeginConfig
 */
type RuleBeginFields = {
    begin: RegExp;
    name?: string;
    beginCaptures?: RuleCapturesConfig<Rule>;
    captures?: RuleCapturesConfig<Rule>;
} & (RuleEndFields | RuleWhileFields);

type RuleEndFields = {
    end: RegExp;
    contentName?: string;
    patterns?: Rule[];
    endCaptures?: RuleCapturesConfig<Rule>;
};

/**
 * @from_type RuleWhileConfig
 */
type RuleWhileFields = {
    while: RegExp;
    whileCaptures?: RuleCapturesConfig<Rule>;
};

const parentSymbol = Symbol("parent");
const patternsSymbol = Symbol("patterns");

const languageName = "less";
const path = `/data/syntaxes/${languageName}/language.gen.json`;

class RuleTypeError extends Error {
    constructor(type?: "begin") {
        const message = type
            ? `If there is 'begin' field, must to be field 'end' or while`
            : `Rule must to be include field 'include' or 'match' or 'begin'`;
        super(message);
    }
}

type RuleTypeConfigType<T> = RuleConfigType<
    T extends RuleType.INCLUDE
        ? RuleIncludeConfig
        : T extends RuleType.MATCH
        ? RuleMathConfig
        : RuleBeginConfigType<
              T extends RuleType.BEGIN_END ? RuleEndConfig : RuleWhileConfig
          >
>;

type RuleTypeRuleGenType<T> = RuleGenType<
    T extends RuleType.INCLUDE
        ? RuleIncludeFields
        : T extends RuleType.MATCH
        ? RuleMathFields
        : RuleBeginFieldsType<
              T extends RuleType.BEGIN_END ? RuleEndFields : RuleWhileFields
          >
>;

function getRuleType(rule: RuleConfig) {
    // console.log({rule});
    if ("include" in rule) {
        return RuleType.INCLUDE;
    } else if ("match" in rule) {
        return RuleType.MATCH;
    } else if ("begin" in rule) {
        if ("end" in rule) {
            return RuleType.BEGIN_END;
        } else if ("while" in rule) {
            return RuleType.BEGIN_WHILE;
        } else {
            throw new RuleTypeError("begin");
        }
    } else {
        throw new RuleTypeError();
    }
}

type Label = string; // имя лейбла, определяется грамматикой
type TokenGroup = number; // группа для несвязанных частей текста, 0 — без группы
type Token = [Label, RichText[], TokenGroup];

// неразмеченный текст либо токен
type RichText = string | Token;

type RichLines = RichText[][]; // строки из несвязанных кусков

/*
    ruleList: Rule[];
превращается:
['line', [
    '    ',
    ['key', 'ruleList', 0],
    ': ',
    ['typeName', 'Rule', 0],
    '[];'
], 0]
пример, как это может выглядеть в html:
<pre>
    <span class="js.line">
        <span>    </span>
        <span class="js.key">ruleList</span>
        <span>: </span>
        <span class="js.typeName">Rule</span>
        <span>[];</span>
    </span>
</pre>
*/

interface CheckLineProps {
    richLine: RichText[];
    matchRule: RuleTypeRuleGenType<RuleType.MATCH>;
}

function checkLine({
    richLine,
    matchRule,
}: CheckLineProps): { richLine: RichText[]; isRaw: boolean } {
    const { match, name, captures } = matchRule;

    let isRaw = false;
    const result: RichText[] = [];

    for (const text of richLine) {
        if (typeof text === "string") {
            // const groups = text.match(match);

            // if (groups === null) {
            //     isRaw = true;
            // }
            const groups: Array<[string, number]> = [];
            const resultText: RichText[] = [];
            let matches: RegExpExecArray;
            let i = 0;
            while ((matches = match.exec(text)) !== null && i < 5) {
                // debugger;
                // console.log(`Found ${matches[0]}. Next starts at ${match.lastIndex}.`);
                console.log(matches, JSON.stringify(match, null, 2));
                groups.push([matches[0], matches.index]);
                i++;
                // expected output: "Found foo. Next starts at 9."
                // expected output: "Found foo. Next starts at 19."
            }
            // console.log({groups});
            if (groups.length > 0) {
                let startIndex = 0;
                for (const group of groups) {
                    const [groupText, index] = group;
                    if (index !== startIndex) {
                        result.push(text.slice(startIndex, index));
                    }
                    result.push([name ?? "", [groupText], 0]);
                    startIndex = index + groupText.length;
                }
                if (startIndex < text.length) {
                    result.push(text.slice(startIndex, text.length));
                }
            } else {
                result.push(text);
            }
        } else {
            result.push(text);
        }
    }

    return {
        richLine: result,
        isRaw: true,
    };
}

interface CheckRuleProps {
    richLines: RichLines;
    rule: Rule;
    repositoryRules: GlobalRepositoryRules;
}

function checkRule({
    richLines,
    rule,
    repositoryRules,
}: CheckRuleProps): { richLines: RichLines; endLineIndex: number } {
    if (rule.disabled) {
        // отключенные правила
        return {
            richLines,
            endLineIndex: 0,
        };
    }

    const type = rule.type;

    // console.log({rule});

    if (type === RuleType.INCLUDE) {
        const includeRule = rule as RuleTypeRuleGenType<RuleType.INCLUDE>;
        const path = includeRule.include;

        // console.log({path});

        if (path[0] === "#") {
            const name = path.slice(1);
            const rules = repositoryRules.get(name);
            const { richLines: lines, endLineIndex } = checkRuleList({
                lines: richLines,
                ruleList: rules,
                repositoryRules,
            });

            return {
                richLines: lines,
                endLineIndex,
            };
        } else if (path[0] === "$") {
            const name = path.slice(1);
            if (name === "self") {
                const rules = repositoryRules.get(patternsSymbol);
                const { richLines: lines, endLineIndex } = checkRuleList({
                    lines: richLines,
                    ruleList: rules,
                    repositoryRules,
                });

                return {
                    richLines: lines,
                    endLineIndex,
                };
            } else if (name === "base") {
                // TODO: достать base
            }
        }
    } else if (type === RuleType.MATCH) {
        const matchRule = rule as RuleTypeRuleGenType<RuleType.MATCH>;
        const { match, name, captures } = matchRule;
        const richLine = richLines[0];
        const {
            richLine: line,
            isRaw, // ещё есть неразобранные куски
        } = checkLine({
            richLine,
            matchRule,
        });
        richLines[0] = line;
        return {
            richLines,
            endLineIndex: isRaw ? 0 : 1,
        };
    }

    return {
        richLines,
        endLineIndex: 0,
    };
}

interface CheckRuleListProps {
    lines: RichLines;
    ruleList: Rule[];
    repositoryRules: GlobalRepositoryRules;
}

function checkRuleList({
    lines,
    ruleList,
    repositoryRules,
}: CheckRuleListProps): { richLines: RichLines; endLineIndex: number } {
    // последняя строка с полностью разобранным текстом
    let endLineIndex = 0;

    let currentRichLines: RichLines = lines;

    // console.log({ruleList});

    for (const rule of ruleList) {
        const { richLines, endLineIndex: endIndex } = checkRule({
            richLines: currentRichLines,
            rule,
            repositoryRules,
        });

        // console.log('rules', {richLines, endIndex});

        // обновляем разобранный текст, рассмотреть мутабельный объект
        currentRichLines = richLines;

        if (endIndex > 0) {
            // смещение по begin/end(while), либо полностью заполнена строка с match
            break;
        }
    }
    return {
        richLines: currentRichLines,
        endLineIndex,
    };
}

interface CheckSyntaxProps {
    lines: string[]; // строки до конца текста
    lineIndex: number;
    charIndex: number;
    repositoryRules: GlobalRepositoryRules;
}

interface ParserProps {
    text: string;
    syntaxConfig: LevelConfig;
    repositoryRules: GlobalRepositoryRules;
}

const parser = ({ text, repositoryRules }: ParserProps): RichLines => {
    // разбиваем текст на строки
    const lines = text.split("\n");

    // текущая строка в исходном тексте
    let lineIndex = 0;

    // текущий символ
    let charIndex = 0;

    // разобранный текст
    const resultRichLines: RichLines = [];

    // текст, подлежащий разбору
    let currentRichLines: RichLines = lines.map((s) => [s]);

    // let currentLineIndex

    while (lineIndex < lines.length) {
        // const line = lines[lineIndex];

        // проверка всех правил
        const {
            richLines, // частично разобранный текст
            endLineIndex, // последняя строка в разобранной части текста, после идёт сырой; если в строке остались неразобранные части, равен 0
        } = checkRuleList({
            lines: lines.slice(lineIndex).map((s) => [s]),
            repositoryRules,
            ruleList: repositoryRules.get(patternsSymbol),
        });
        console.log({ richLines, endLineIndex });

        // добавляем разобранную часть к остальным
        resultRichLines.push(
            ...richLines.slice(0, endLineIndex > 0 ? endLineIndex : 1)
        );

        // последняя разобранная строка в общем тексте
        const endIndex = lineIndex + endLineIndex;

        // двигаемся дальше по тексту
        lineIndex =
            endIndex > lineIndex
                ? endIndex // продвинулись за счет правил begin/end(while, если не было пустот)
                : lineIndex + 1; // в текущей строке остался неразобранный текст, но правила кончились
        // charIndex = endCharIndex;
    }
    console.log({ resultRichLines });
    return resultRichLines;
};

type GlobalRepositoryConfig = Map<string | Symbol, RuleConfig[]>;

function addRules(
    globalRepository: GlobalRepositoryConfig,
    key: string | Symbol,
    Rules: RuleConfig[]
): void {
    if (globalRepository.has(key)) {
        throw new Error(`Такое правило уже есть: ${key}`);
    } else {
        globalRepository.set(key, Rules);
    }
}

function isRepositoryConfig(config: RuleConfig | LevelConfig): boolean {
    if ("repository" in config) {
        return true;
    }
    if ("patterns" in config && Object.keys(config).length === 1) {
        return true;
    }
    return false;
}

/**
 * преобразуем вложенные репозитории в линейную структуру
 * @param config уровень конфига
 * @param globalRepository мутабельный объект, в него записываются правила
 * @param name имя для группы правил
 */
const generateGlobalRepository = (
    config: LevelConfig,
    globalRepository: GlobalRepositoryConfig,
    name: string | Symbol
) => {
    if ("repository" in config) {
        const repository = config.repository;
        for (const key of Object.keys(repository)) {
            const subTree = repository[key];
            subTree[parentSymbol] = repository;

            if (isRepositoryConfig(subTree)) {
                // именованный репозиторий
                generateGlobalRepository(
                    subTree as LevelConfig,
                    globalRepository,
                    key
                );
            } else {
                // именованное правило
                addRules(globalRepository, key, [subTree as RuleConfig]);
            }
            // удаляем поддерево, все данные из него мы перенесли
            delete repository[key];
        }
    }
    if ("patterns" in config) {
        // именованная группа правил
        addRules(globalRepository, name, config.patterns);
    }
};

function prepareRegex(s: string): string {
    const list = s.split("\n");
    if (s.indexOf("\\#") !== -1) {
        console.log(s);
        // есть комментарии, убираем их
        const result = list
            .map((e) => e.trim())
            .map((row) => {
                const startCommentIndex = row.search(/(?<!\\)#/);
                console.log(startCommentIndex);
                return (startCommentIndex === -1
                    ? row
                    : row.slice(0, startCommentIndex)
                ).trim();
            })
            .join("");
        console.log(result);
        return result;
    } else {
        return list.join("");
    }
}

function getRegex(s: string): RegExp {
    try {
        const regexString = prepareRegex(s)
            .replace(/\[\:alnum\:\]/g, "[\\d\\w]")
            .replace(/\[\:alpha\:\]/g, "[\\w]")
            .replace(/\(\?x\)/, "");
        // console.log({s, regexString});
        return new RegExp(regexString, "g");
    } catch (e) {
        console.error(e);
        console.log(s);
        // TODO: временная мера, пока нет парсера ruby regex
        return /EMPTY/;
        throw new Error(`Regexp doesnt to be created from string '${s}'`);
    }
}

type GlobalRepositoryRules = Map<string | Symbol, Rule[]>;

// отмечать
type RulesDependenciesMap = Map<string, string>;

function setMeta(rule: Rule, config: RuleConfig) {
    if ("comment" in config) {
        rule.comment = config.comment;
    }
    if ("disabled" in config) {
        rule.disabled = config.disabled;
    }
}

interface SetCapturesProps {
    rule: RuleGenType<RuleMathFields | RuleBeginFields>;
    capturesName: string;
    captures: RuleCapturesConfig<RuleConfig>;
    key: string;
    globalRepository: GlobalRepositoryConfig;
}

function setCaptures({
    rule,
    captures,
    capturesName,
    key,
    globalRepository,
}: SetCapturesProps): void {
    // mutable rule
    rule[capturesName] = Object.keys(captures).reduce((res, groupKey) => {
        res[groupKey] = {};
        return res;
    }, {});
    for (const groupKey of Object.keys(captures)) {
        const groupConfig = captures[groupKey];
        const groupRule = rule[capturesName][groupKey];
        const name = groupConfig.name;
        const patterns = groupConfig.patterns;
        if (name) {
            groupRule.name = name;
        }
        if (patterns) {
            groupRule.patterns = patterns.map((pattern, i) => {
                return createRule({
                    level: 1,
                    key: `${key}/:${capturesName}/${groupKey}/${i}`,
                    ruleConfig: pattern,
                    globalRepository,
                });
            });
        }
    }
}

interface CreateRuleProps {
    level: number;
    key: string;
    ruleConfig: RuleConfig;
    rulesDependenciesMap?: RulesDependenciesMap;
    globalRepository: GlobalRepositoryConfig;
}

function createRule({
    level = 0,
    key,
    ruleConfig,
    rulesDependenciesMap,
    globalRepository,
}: CreateRuleProps): Rule {
    const type = getRuleType(ruleConfig);
    return {
        [RuleType.INCLUDE]: () => {
            const config = ruleConfig as RuleTypeConfigType<RuleType.INCLUDE>;
            const path = config.include;
            if (path[0] === "#") {
                const ruleName = path.slice(1);
                if (!globalRepository.has(ruleName)) {
                    throw new Error(
                        `Rule '${ruleName}' doesn't exist in repository.`
                    );
                }
                if (level === 0) {
                    // для нулевого уровня вложенности проверяем циклические зависимости
                    if (rulesDependenciesMap.has(ruleName)) {
                        const dependence = rulesDependenciesMap.get(ruleName);
                        if (dependence === key) {
                            throw new Error(
                                `There is circular dependence between ${key} and ${ruleName} rules.`
                            );
                        }
                    }
                    // фиксируем зависимость
                    rulesDependenciesMap.set(key, ruleName);
                }
                const rule = {} as RuleTypeRuleGenType<RuleType.INCLUDE>;
                rule.type = RuleType.INCLUDE;
                setMeta(rule, config);
                rule.include = config.include;

                return rule;
            } else if (path[0] === "$") {
                const name = path.slice(1);
                if (!["self", "base"].includes(name)) {
                    throw new Error(
                        `Grammar alias must to be equal $self or $base`
                    );
                }
            } else {
                // throw new Error(`Include rule must to be start with '#' or '$'`);
            }
        },
        [RuleType.MATCH]: () => {
            const config = ruleConfig as RuleTypeConfigType<RuleType.MATCH>;
            const name = config.name;
            const captures = config.captures;
            // if (!(name || captures)) {
            //     throw new Error(`The rule '${key}' doesn't include fields 'name' and/or 'captures'`);
            // }
            const rule = {} as RuleTypeRuleGenType<RuleType.MATCH>;
            rule.type = RuleType.MATCH;
            setMeta(rule, config);
            rule.match = getRegex(config.match);
            if (name) {
                rule.name = config.name;
            }
            if (captures) {
                setCaptures({
                    rule,
                    captures,
                    capturesName: "captures",
                    key,
                    globalRepository,
                });
            }
            return rule;
        },
        [RuleType.BEGIN_END]: () => {
            const config = ruleConfig as RuleTypeConfigType<RuleType.BEGIN_END>;
            const rule = {} as RuleTypeRuleGenType<RuleType.BEGIN_END>;
            setMeta(rule, config);
            const {
                begin,
                end,
                name,
                contentName,
                patterns,
                beginCaptures,
                endCaptures,
            } = config;
            rule.begin = getRegex(begin);
            rule.end = getRegex(end);
            if (name) {
                rule.name = name;
            }
            if (contentName) {
                rule.contentName = contentName;
            }
            if (patterns) {
                rule.patterns = patterns.map((pattern, i) => {
                    return createRule({
                        level: 1,
                        key: `${key}/:patterns/${i}`,
                        ruleConfig: pattern,
                        globalRepository,
                    });
                });
            }
            if (beginCaptures) {
                setCaptures({
                    rule,
                    captures: beginCaptures,
                    capturesName: "beginCaptures",
                    key,
                    globalRepository,
                });
            }
            if (endCaptures) {
                setCaptures({
                    rule,
                    captures: endCaptures,
                    capturesName: "endCaptures",
                    key,
                    globalRepository,
                });
            }
            return rule;
        },
        [RuleType.BEGIN_WHILE]: () => {
            const config = ruleConfig as RuleTypeConfigType<
                RuleType.BEGIN_WHILE
            >;
            const rule = {} as RuleTypeRuleGenType<RuleType.BEGIN_WHILE>;
            setMeta(rule, config);
            const {
                begin,
                while: whileString,
                name,
                beginCaptures,
                whileCaptures,
            } = config;
            rule.begin = getRegex(begin);
            rule.while = getRegex(whileString);
            if (name) {
                rule.name = name;
            }
            if (beginCaptures) {
                setCaptures({
                    rule,
                    captures: beginCaptures,
                    capturesName: "beginCaptures",
                    key,
                    globalRepository,
                });
            }
            if (whileCaptures) {
                setCaptures({
                    rule,
                    captures: whileCaptures,
                    capturesName: "whileCaptures",
                    key,
                    globalRepository,
                });
            }
            return rule;
        },
    }[type]();
}

// превращаем строки в регулярные выражения
function generateRepositoryRules(
    globalRepository: GlobalRepositoryConfig
): GlobalRepositoryRules {
    const repository: GlobalRepositoryRules = new Map();
    // карта отмеченных правил для избежания циклических ссылок
    const rulesDependenciesMap: RulesDependenciesMap = new Map();
    for (const key of globalRepository.keys()) {
        const rules: Rule[] = [];
        for (const ruleConfig of globalRepository.get(key)) {
            const ruleKey = typeof key === "string" ? key : ":root";
            const rule = createRule({
                level: 0,
                key: ruleKey,
                ruleConfig,
                rulesDependenciesMap,
                globalRepository,
            });
            if (rule) {
                rules.push(rule);
            }
        }
        repository.set(key, rules);
    }
    return repository;
}

